let amapFile = require('../../common/amap-wx.js');
let myAmapFun = new amapFile.AMapWX({
	key: '2649cf46ba2417b42f9fe36f53c57f63'
});
export default {
	data() {
		
		return {
			keywords:'',
			show: true,
			markers: [],
			latitude: 23.13,
			longitude: 113.27,
			city: '',
			myAmapFunflag: '',
			dataArr: [],
			labels:[]
		}
	},
	onLoad() {
		uni.getLocation({
		    type: 'gcj02',
		    success:  (res) =>{
				console.log(res)
		        console.log('当前位置的经度：' + res.longitude);
		        console.log('当前位置的纬度：' + res.latitude);
				let markers=[]
				myAmapFun.getRegeo({
					iconPath: '../../static/img/marker.png',
					location: res.longitude + ',' + res.latitude, //经纬度
					success: (data) => {
						console.log(data)
						// let markersData = data.markers;
				
						this.markers = [{
							id: data[0].id,
							latitude: data[0].latitude,
							longitude: data[0].longitude,
							iconPath: data[0].iconPath,
							width: 40,
							height: 60,
							label:{
								content:data[0].name,
								fontSize:16,
								bgColor:'#fff',
								textAlign:'right',
								borderRadius:5,
								padding:3,
								color:'red',
								width:200
							}
						}]
				
						this.latitude = data[0].latitude
				
						this.longitude = data[0].longitude
						// that.showMarkerInfo(markersData, 0);
					},
					fail: function(info) {
						uni.showModal({
							title: info.errMsg
						})
					}
				})
				
				
		    }
		});

	},
	methods: {
		changeMarkerColor(data, i) {
			console.log("data", data)
			
			
			uni.showModal({
				content: "确定更改地址?",
				success: (res) => {
					if (res.confirm) {
						let that = this;
						let markers = [];
						let labels=[]
						for (let j = 0; j < data.length; j++) {
							if (j == i) {
								data[j].iconPath = "../../static/img/marker_checked.png";
							} else {
								data[j].iconPath = "../../static/img/marker.png";
							}
							markers.push({
								id: data[j].id,
								latitude: data[j].latitude,
								longitude: data[j].longitude,
								iconPath: data[j].iconPath,
								width: data[j].width,
								height: data[j].height,
								label:{
									content:data[j].label.content,
									fontSize:16,
									bgColor:'#fff',
									textAlign:'right',
									borderRadius:5,
									padding:3,
									color:'red'
								}
							})
						
						}
						this.latitude = data[0].latitude
						
						this.longitude = data[0].longitude
						this.markers = markers
						console.log("可以了")
					}
				}
			})
		},
		makertap(e) {
			let id = e.markerId;
			let that = this;
			// that.showMarkerInfo(this.markers,id);
			that.changeMarkerColor(this.markers, id);

			console.log("1221")
		},
		selectAdress(item) {

			console.log(item)
			if(item.location.length===0){
				uni.showToast({
					title:'请输入具体地址',
					icon:'none'
				})
				return false
			}
			this.longitude = item.location.split(',')[0]
			this.latitude = item.location.split(',')[1]
			this.show = false
			this.myAmapFunflag.getRegeo({
				iconPath: '../../static/img/marker.png',
				location: this.longitude + ',' + this.latitude, //经纬度
				success: (data) => {
					console.log(data)
					// let markersData = data.markers;

					this.markers = [{
						id: data[0].id,
						latitude: data[0].latitude,
						longitude: data[0].longitude,
						iconPath: data[0].iconPath,
						width: 40,
						height: 60,
						label:{
							content:data[0].desc,
							fontSize:16,
							bgColor:'#fff',
							textAlign:'right',
							borderRadius:5,
							padding:3,
							color:'red'
						}
					}]

					this.latitude = data[0].latitude

					this.longitude = data[0].longitude
					// that.showMarkerInfo(markersData, 0);
				},
				fail: function(info) {
					uni.showModal({
						title: info.errMsg
					})
				}
			})
			console.log(this.myAmapFunflag)
		},


		bindInput(e) {
			if (!e.detail.value) {
				console.log("212121")
				this.dataArr = []
				return false
			}
			
			this.myAmapFunflag = myAmapFun
			console.log(this)
			let that = this
			myAmapFun.getInputtips({
				keywords: e.detail.value,
				success: (data) => {
					console.log(data.tips)

					that.dataArr = data.tips
					console.log(that.dataArr)
				}
			})
			let params = {
				iconPathSelected: '../../static/img/marker_checked.png',
				iconPath: '../../static/img/marker.png',
				success: function(data) {
					console.log(data)
				},
			}
			myAmapFun.getPoiAround(params)

		}
	}
}
